<template>
    <b-modal
      id="modal-android"
      :hide-footer="true"
      :hide-header="true"
      centered
      title="BootstrapVue"
      no-close-on-backdrop
      no-close-on-esc
      v-model="show">
      <div class="modal-close" v-on:click="$emit('closeOwnersList', false)">
        <span>X</span>
      </div>
      <div class="title-container">
        <!-- <p class="subtitle">{{ subtitle }}</p> -->
        <p class="title">{{ dir === 'ltr' ? 'Select Owner' : 'إختار مالك' }}</p>
      </div>
      <hr />
      <div class="search-field-container d-flex align-items-center">
        <svg-icon
          class="search-icon"
          name="magnifying-glass-grey"
          width="24"
          height="24"></svg-icon>
        <input class="dropdown-search" v-model="searchQuery" />
        <span class="search-clear" v-if="searchQuery !== ''" v-on:click="searchQuery = ''">Clear</span>
      </div>
      <div class="options-container">
        <div :class="ownerId && ownerId === option.id ? 'option selected' : 'option'" v-for="option in filterOptions" v-on:click="chooseOption(option)" :key="option.id">
          <div class="option-label">{{ option.name }}</div>
          <div class="option-circle"><div class="inner-circle"></div></div>
        </div>
      </div>
    </b-modal>
</template>

<script>
import $axios from '~/helpers/axios-port';

import { createNamespacedHelpers } from 'vuex'
const { mapState } = createNamespacedHelpers('PropertyModule')

export default {
    name: "selectOwner",
    props:{
      show: {
        type: Boolean,
        default: false
      },
      optionsData: {
        type: Array,
      },
      propOwnerId: [Number, String]
    },
    data(){
        return {
            ownerId: null,
            searchQuery: "",
            filterOptions: []
        }
    },
    computed:{
      ...mapState(['clickedPropToAttachOwner']),
      dir() {
        return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
      }
    },
    methods:{
      async chooseOption(option){
        this.ownerId = option.id;
        await this.attachPropToOwner();
        this.$emit('closeOwnersList', false);
        this.searchQuery = '';
      },
      async attachPropToOwner(){
        const res = await this.$axios.$post(`/user/owners/attach/${this.clickedPropToAttachOwner.id}/owner/${this.ownerId}`);
        if(res.Status === 200){
          this.$bvToast.toast(`Property Attached To Owner successfully.`, {
            autoHideDelay: 3000,
            appendToast: false,
            toaster: 'b-toaster-top-full',
            variant: 'success',
          });
          this.$emit('reCallProperties')
        }
      },
      async getUserOwners(){
        const res = await this.$axios.$get(`/user/owners`);
        this.filterOptions = res.Data;
      },
    },
    watch: {
        searchQuery(val){
          this.filterOptions = this.optionsData.filter(option => option.name.toLowerCase().indexOf(val.toLowerCase()) >= 0)
        }
    },
    mounted(){
      this.filterOptions = this.optionsData;
      this.ownerId = this.propOwnerId;
      this.getUserOwners()
    }
}
</script>

<style lang="scss">
#modal-android {
  .modal-content {
    background-color: #ffffff;
    border-radius: 10px;
    hr {
      margin: 16px -15px;
    }
    .title-container {
      .subtitle {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #48535b;
        margin-bottom: 4px;
      }
      .title {
        font-weight: 600;
        font-size: 16px;
        line-height: 16px;
        color: #111111;
        margin-bottom: 0;
      }
    }
    .search-field-container {
      position: relative;
      margin-bottom: 24px;
      gap: 20px;
      svg {
        position: absolute;
        left: 5px;
        top: 5px;
      }
      .dropdown-search {
        width: 100%;
        background: transparent;
        color: black;
        padding: 5px 35px;
        font-size: large;
        height: 38px;
        border: 1px solid #e3e5e5;
        border-radius: 8px;
      }
      .search-clear {
        border: 1px solid #000;
        padding: 2px;
        border-radius: 3px;
        cursor: pointer;
      }
    }
    .option {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      padding: 15px 0;
      &:not(:last-child) {
        border-bottom: lightgray 1px solid;
      }
      .option-circle {
        width: 20px;
        height: 20px;
        background-color: transparent;
        border-radius: 50%;
        border: 2px solid $primary;
        margin: auto 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
      .option-label {
        max-width: calc(100% - 20px);
        cursor: default;
        font-weight: 400;
        font-size: 16px;
        line-height: 16px;
        color: #111111;
      }
      &.selected {
        .inner-circle {
          width: 10px;
          height: 10px;
          background: $primary;
          border-radius: 50%;
          margin: auto;
        }
      }
    }
  }
  .modal-close {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    position: absolute;
    right: 10px;
    top: 10px;
    width: 36px;
    height: 36px;
    background: #f7f9fa;
    border-radius: 100px;
    cursor: pointer;
    span {
      color: black;
      font-size: large;
      font-weight: bolder;
      text-align: center;
    }
  }
}
</style>