<template>
  <div>
    <div class="container pb-3 bg-white">
      <div class="header-wrapper d-flex justify-content-start flex-column flex-md-row justify-content-md-between align-items-md-center">
        <Header icon="inactive_my_properties.svg" class="mb-2">
          <template slot="title">{{ $t('header.my_properties') }}</template>
          <template slot="subtitle">
            {{ dir === 'ltr' ? 'Manage all your properties in one place' : 'قم بإدارة جميع عقاراتك من مكان واحد' }} 
          </template>
        </Header>
        <b-button pill variant="primary" class="text-center" :to="localePath('/marketplace')" size="sm">
          <img
            width="20px"
            height="20px"
            :class="[dir === 'rtl' ? 'ml-2' : 'mr-2']"
            :src="require('@/assets/images/sidebar/Storefront2.svg')"
            alt="photo" />
          <span class="mx-2">{{ $t('marketplace.title') }}</span>
        </b-button>
      </div>
    </div>

    <div class="properties-container">
      <div class="container">
        <b-overlay :show="myPropertiesPage.loading" rounded="sm" :opacity="0.5">
          <h2 class="font-weight-bold pt-4 px-3" style="color: #111">
            {{ meta.total }} {{ dir === 'ltr' ? ' properties found' : 'عقارات وجدت' }}
          </h2>

          <div class="property-container">
            <div
              class="text-center"
              v-if="!myPropertiesPage && !myPropertiesPage.data">
              {{ $t('common.noProperties') }}
            </div>

            <b-row class="mx-0" v-else>
              <b-col
                lg="4"
                md="6"
                cols="12"
                v-for="(item, index) in myPropertiesPage.data"
                :key="index">
                <PropertyCard @reCallProperties="reCallProperties" :userType="userType" :item="item" />
              </b-col>
            </b-row>
          </div>
          <Pagination :meta="meta" />
        </b-overlay>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '@/components/dashboard/Header';
import PropertyCard from '@/components/dashboard/my-properties/PropertyCard';
import PropertySort from '@/components/property/PropertySort';

export default {
  name: 'MyProperties',
  components: {
    Header,
    PropertyCard,
    PropertySort,
  },
  watchQuery: true,
  async asyncData({ $axios, query }) {
    const queries = { ...query, per_page: 10 };
    const [myPropertiesResponse, allPropertiesResponse] = await Promise.all([
      $axios.get(`/user/getMyProperties`, {
        params: { ...queries },
      }),
      $axios.$get('/user/getAllUserProperties'),
    ]);

    const { collection, pagination } = myPropertiesResponse.data.Data;
    return {
      allProperties: allPropertiesResponse.Data,
      myPropertiesPage: {
        data: collection,
        loading: false,
      },
      meta: { ...pagination },
    };
  },
  data() {
    return {
      show: false,
      userType: '',
    };
  },
  methods:{
    test(e){
      this.show = e;
    },
    async reCallProperties(){
      await this.$nuxt.refresh();
    },
    async isbroker(){
      const { Data } = await this.$axios.$get('/user/userDetails');
      this.userType = Data.type;
    }
  },
  head() {
    return {
      title: `${this.dir === "ltr" ? "My Properties" : "عقاراتى"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? "My Properties" : "عقاراتى",
        }
      ]
    }
  },
  computed:{
    appEnv(){
      return this.$config.APP_ENV
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
  },
  mounted(){
    this.isbroker();
  }
};
</script>
<style lang="scss" scoped>
.header-wrapper {
  gap: 20px
}
.properties-container {
  background-color: #f7f9fa;
}

.divider {
  width: 95%;
  height: 1px;
  background-color: #000;
}
</style>
