<template>
  <div class="property-card">
    <NuxtLink :to="localePath(`/rent/${item.slug}?term=${item.term}`)">
      <div class="card-images">
        <ImageSlider :images="item.images" />
      </div>
    </NuxtLink>
    <div class="card-content">
      <div class="container">
        <NuxtLink :to="localePath(`/rent/${item.slug}?term=${item.term}`)">
          <div class="card-title">
            {{ item.name | displayExcerpt(40) }}
          </div>
          <div class="card-amenities">
            <div class="amenity">
              <svg-icon name="grey-bed" width="16" height="16" fill="#48535b" />
              <span> {{ item.no_of_bedroom }} bed </span>
            </div>
            <div class="amenity">
              <svg-icon name="grey-bath" width="16" height="16" fill="#48535b" />
              <span> {{ item.no_of_bathroom }} bath </span>
            </div>
            <div class="amenity" v-if="item.area">
              <svg-icon name="grey-bath" width="16" height="16" fill="#48535b" />
              <span> {{ item.area }} {{ $t('property.propertyDetails.squareMeters') }} </span>
            </div>
          </div>
          <div class="card-price" v-if="item.min_daily_price !== '0'">
            <span class="price">
              {{
                ` ${item.min_daily_price} - ${
                  item.max_daily_price
                } ${$t('common.egp')}`
              }}
            </span>
            <span class="light-gray font-size-small">/ {{ dir === 'ltr' ? 'Day' : 'يوم' }} </span>
          </div>
          <div class="card-price" v-if="item.min_monthly_price !== '0'">
            <span class="price">
              {{
                ` ${item.min_monthly_price} - ${
                  item.max_monthly_price
                } ${$t('common.egp')}`
              }}
            </span>
            <span class="light-gray font-size-small">/ {{ dir === 'ltr' ? 'Month' : 'شهر' }}</span>
          </div>
        </NuxtLink>
        <div class="card-actions">
          <div class="btn">
            <b-form-checkbox
              v-if="!statusLoading"
              v-model="itemStatus"
              @change="updateStatus">
              Available
            </b-form-checkbox>
            <b-spinner
              v-else
              label="Spinning"
              variant="primary"
              small></b-spinner>
          </div>
          <div class="vertical-line"></div>
          <div
            class="btn"
            @click="
              () =>
                $router.push(
                  localePath(`/dashboard/properties/${item.id}/edit`)
                )
            ">
            <svg-icon name="edit" width="18" height="18" fill="#111"></svg-icon>
            {{ $t('common.editProperty') }}
          </div>
          <div class="vertical-line"></div>
          <div v-if="userType === 'corporate'" class="btn moreActions" @click="showModal_SaveProp(item)"> 
            <img class="mx-0" width="30px" height="30px" src="@/assets/images/icons/more-actions.svg" alt="More Actions">
            <span>{{ $t('common.moreActions') }}</span>
          </div>
          <div class="btn delete" v-else @click="deleteProperty(item.id)">
            <!-- <b-button block class="delete"> -->
              <img :class="[dir === 'ltr' ? 'mr-1' : 'ml-1']" width="20px" height="20px" src="@/assets/images/icons/delete-red.svg" alt="Delete">
              <span>{{ $t('property.delete_Property') }}</span>
            <!-- </b-button> -->
          </div>
        </div>
        <b-modal :id="'moreActions-modal-'+item.id" hide-footer hide-header>
          <b-row>
            <b-col cols="12" md="6" class="p-2">
              <b-button block class="bg-primary text-white addOwner" @click="enableCreateModeFunc()">
                <!-- :to="localePath('/dashboard/owner-information')" -->
                <img :class="[dir === 'ltr' ? 'mr-1' : 'ml-1']" width="20px" height="20px" src="@/assets/images/icons/plus-circle-white-2.svg" alt="Plus Add Circle">
                {{ $t('owner.add_New_Owner') }}
              </b-button>
            </b-col>
            <b-col cols="12" md="6" class="p-2">
              <b-button block variant="outline-primary" @click="show = true">
                <img :class="[dir === 'ltr' ? 'mr-1' : 'ml-1']" width="20px" height="20px" src="@/assets/images/icons/plus-circle-white.svg" alt="Plus Add Circle">
                {{ $t('owner.select_From_Owners_List') }}
              </b-button>
              <selectOwner @reCallProperties="$emit('reCallProperties')" :propOwnerId="item.owner_id" @closeOwnersList="closeOwnerList($event)" :show="show"/>
            </b-col>
            <b-col cols="12" class="p-2" v-if="item.owner_id">
              <b-button block variant="outline-primary" @click="disableCreateModeFunc(item.owner_id)">
                <img :class="[dir === 'ltr' ? 'mr-1' : 'ml-1']" width="20px" height="20px" src="@/assets/images/icons/edit.svg" alt="Plus Add Circle">
                {{ $t('owner.edit_Owner_Details') }}
              </b-button>
            </b-col>
            <b-col cols="12" class="p-2" v-if="item.owner_id">
              <b-button block class="delete edit" @click="unSelectOwner(item.id, item.owner_id)">
                <img :class="[dir === 'ltr' ? 'mr-1' : 'ml-1']" width="20px" height="20px" src="@/assets/images/icons/unselect.svg" alt="Plus Add Circle">
                {{ $t('owner.unselect_Owner') }}
              </b-button>
            </b-col>
            <div class="divider my-2 mx-auto"></div>
            <b-col cols="12" class="p-2">
              <b-button block class="delete" @click="deleteProperty(item.id)">
                <img :class="[dir === 'ltr' ? 'mr-1' : 'ml-1']" width="20px" height="20px" src="@/assets/images/icons/delete-red.svg" alt="Plus Add Circle">
                {{ $t('property.delete_Property') }}
              </b-button>
            </b-col>
          </b-row>
        </b-modal>
      </div>
    </div>
  </div>
</template>

<script>
import ImageSlider from '@/components/property/ImageSlider';
import $axios from '~/helpers/axios-port';
import { createNamespacedHelpers } from 'vuex'
const { mapActions } = createNamespacedHelpers('PropertyModule')
const { mapActions: userActions } = createNamespacedHelpers('UserModule')
import selectOwner from '@/components/shared/form/selectOwner.vue';


export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
    userType: {
      type: String,
    }
  },
  components: {
    ImageSlider,
    selectOwner,
  },
  computed:{
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    }
  },
  data() {
    return {
      itemStatus: null,
      statusLoading: false,
      show: false,
    };
  },
  methods: {
    ...userActions(['enableCreateMode', 'disableCreateMode', 'Set_editOwnerId']),
    ...mapActions(['set_clickedPropToAttachOwner']),
    enableCreateModeFunc(){
      this.enableCreateMode()
      this.$router.push(this.localePath('/dashboard/owner'));
    },
    disableCreateModeFunc(ownerId){
      this.disableCreateMode()
      this.Set_editOwnerId(ownerId)
      this.$router.push(this.localePath('/dashboard/owner'));
    },
    async updateStatus() {
      try {
        this.statusLoading = true;
        const data = {
          id: this.item.id,
          status: this.itemStatus ? 1 : 0,
          type: 'myProperties',
        };
        await this.$axios.$post('/user/updatePropertyStatus', data);

        this.$bvToast.toast(`${this.item.name} status updated successfully.`, {
          autoHideDelay: 2000,
          appendToast: false,
          variant: 'success',
        });

        this.statusLoading = false;
      } catch (error) {
        this.$bvToast.toast(`Error occurred when updating property status`, {
          autoHideDelay: 2000,
          appendToast: false,
          variant: 'danger',
        });
        this.statusLoading = false;
      }
    },
    showModal_SaveProp(item){
      this.set_clickedPropToAttachOwner(item);
      localStorage.setItem('clickedProperty', JSON.stringify(item))
      this.$bvModal.show(`moreActions-modal-${item.id}`)
      // this.$root.$emit('bv::show::modal', 'moreActions-modal')
    },
    closeOwnerList(e){
      this.show = e;
    },
    async unSelectOwner(propId, ownerId){
      const res = await this.$axios.$post(`/user/owners/deattach/${propId}/owner/${ownerId}`);
      if(res.Status === 200){
        this.$bvToast.toast(`Property Deattached From Owner successfully.`, {
          autoHideDelay: 3000,
          appendToast: false,
          toaster: 'b-toaster-top-full',
          variant: 'danger',
        });
      }
      this.$emit('reCallProperties')
    },
    async deleteProperty(id){
      const res = await $axios.$delete(`/user/properties/${id}`);
      if(res.Status == 200){
        this.$bvToast.toast(`Property Deleted successfully.`, {
          autoHideDelay: 3000,
          appendToast: false,
          toaster: 'b-toaster-top-full',
          variant: 'danger',
        });
        this.$bvModal.hide(`moreActions-modal-${id}`)
        this.$emit('reCallProperties')
      }
    },
  },
  created() {
    this.itemStatus = this.item?.status ? true : false;
  },
};
</script>

<style lang="scss">
#moreActions-modal___BV_modal_content_.modal-content {
  width: 600px !important;
}

.modal[id^="moreActions-modal"] .modal-dialog{
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin-bottom: 0;
}

div[id^="moreActions-modal"] {
  .btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 20px;
    color: #0040ff;
    background-color: #e6f2ff;
    font-weight: bold;
    font-size: 12px;
    // border: none;
    &:not(.addOwner):hover {
      background-color: rgba(0, 64, 255, .2) !important;
    }
    &.delete {
      background-color: rgba(220, 53, 69, .25) !important;
      color: #db0909 !important;
      transition: .5s !important;
      &.edit {
        background-color: transparent !important;
      }
      &:hover {
        background-color: rgba(220, 53, 69, .45) !important;
      }
    }
  }
}

.property-card {
  margin: 16px 0;
  background-color: #fff;
  border: 1px solid #e8e8e8;
  border-radius: 16px;

  .card-images {
    div {
      border-radius: 16px 16px 0 0;
    }
  }

  .card-content {
    .container {
      .card-title {
        font-size: 16px;
        font-weight: bold;
        margin: 10px 0 8px;

        a {
          color: #111;
        }
      }

      .card-amenities {
        display: flex;

        .amenity {
          font-size: 13px;
          color: rgba(38, 45, 61, 0.64);

          &:nth-child(2) {
            margin: 0 16px;
          }
        }
      }

      .card-price {
        margin: 16px 0;

        .price {
          color: #0078ff;
          font-weight: bold;
          font-size: 16px;
        }

        .font-size-small {
          font-size: 14px;
        }
      }

      .card-actions {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 16px 0;
        border-top: 1px solid #e8e8e8;

        .btn {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          text-decoration: none;
          font-size: 16px;
          color: #111;
          padding: 4px 0 !important;
          &.delete {
            font-size: 12px !important;
          }
          
          .custom-control {
            padding-left: 8px;
            padding-right: 8px;
          }
          &.moreActions {
            span {
              font-size: 10px !important;
              font-weight: bold;
            }
          }
          img,
          .icon {
            margin: 0 8px 2px 8px;
          }
        }

        .vertical-line {
          width: 1px;
          height: 36px;
          background-color: #e8e8e8;
        }
      }
    }
  }
}
</style>
