<template>
  <div class="sort-container">
    <b-overlay :show="show" fixed no-wrap></b-overlay>
    
    <b-dropdown right>
      <template #button-content>
        <span>{{ selected && selected.name }}</span>
        <img
          width="16px"
          height="16px"
          class="arrow-icon ml-1"
          :src="require('@/assets/images/sidebar/CaretRight.svg')" />
      </template>
      <b-dropdown-item
        v-for="(option, index) in options"
        :key="index"
        @click="sort(option)" class="px-1">
        {{ option.name }}
      </b-dropdown-item>
    </b-dropdown>
  </div>
</template>

<script>
export default {
  name: 'PropertySort',
  data() {
    return {
      //options of sort by
      options: [
        {
          name: this.$t('property.sortBy'),
        },
        {
          name: this.$t('property.dailyAscendingPrice'),
          sort_key: 'asc',
          sort_price: 'daily_price'
        },
        {
          name: this.$t('property.dailyDescendingPrice'),
          sort_key: 'desc',
          sort_price: 'daily_price'
        },
        { 
          name: this.$t('property.monthlyAscendingPrice'),
          sort_key: 'asc',
          sort_price: 'monthly_price'
        },
        { 
          name: this.$t('property.monthlyDescendingPrice'),
          sort_key: 'desc',
          sort_price: 'monthly_price'
        },
      ],
      selected: null,
      show: false
    };
  },
  methods: {
    async sort(option) {
      if(this.selected.name !== option.name){
        this.show = true;
        this.selected = option;
        if(this.selected.name === 'Sort by' || this.selected.name === 'صنف حسب'){
          var { Data } = await this.$axios.$get(`/searchProperty`, {
            params: {
              location_id: this.$store.state.SearchModule.searchLocationId || localStorage.getItem('searchLocationId'),
            }
          });
          this.show = false;
        }else{
          var { Data } = await this.$axios.$get(`/searchProperty`, {
            params: {
              location_id: this.$store.state.SearchModule.searchLocationId || localStorage.getItem('searchLocationId'),
              sort_price: option.sort_price,
              sort_key: option.sort_key
            }
          });
          this.show = false;
        }
        this.$emit('getSortedProperties', Data)
      }
      
    },
    makeToast() {
      this.$bvToast.toast('Waiting to get Sorted Properties', {
        title: `Sorted Properties`,
        variant: info,
        solid: true
      })
    }
  },
  //defult txt in drop down is sort by wich have no value
  mounted() {
    this.selected = this.options[0];
  },
};
</script>

<style lang="scss">
.sort-container {
  width: 230px;

  .dropdown {
    width: 100%;

    .btn {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 6px 12px;
      color: #111;
      font-weight: bold;
      background-color: transparent;
      border: 2px solid #111;
      border-radius: 8px;
      // padding-left: 8px !important;
      // padding-right: 8px !important;
      img {
        transform: rotate(90deg);
      }
    }
  }
}
</style>
